import React from 'react';
import styled from '@kevinastone/theme-blog/styled';
import {graphql} from 'gatsby';
import {SitePageContext, Unnamed_1_Query} from '../../graphqlTypes';
import slugify from '../../utils/slug';
import Layout from '../layout';
import Comments from '../comments';
import SEO from '../seo';
import BlogHeading from './heading';

const BlogContent = styled.article`
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    color: ${props => props.theme.colors.primary};
  }
`;

interface Props {
  data: Unnamed_1_Query;
  pageContext: SitePageContext;
}

export default ({data: {markdownRemark: post}, pageContext}: Props) => {
  return (
    <Layout header={<BlogHeading {...post!.frontmatter!} />}>
      <SEO
        title={post!.frontmatter!.title!}
        description={post!.excerpt!}
        keywords={post!.frontmatter!.tags!}
      />
      <BlogContent dangerouslySetInnerHTML={{__html: post!.html!}} />
      <Comments
        id={slugify(pageContext!.slug!)}
        title={post!.frontmatter!.title!}
      />
    </Layout>
  );
};

export const query = graphql`
  query($slug: String!) {
    markdownRemark(fields: {slug: {eq: $slug}}) {
      html
      excerpt
      frontmatter {
        ...Heading
        title
        tags
      }
    }
  }
`;
