import idx from 'idx.macro';
import React from 'react';
import {graphql} from 'gatsby';
import {css} from '@emotion/core';
import styled from '@kevinastone/theme-blog/styled';
import {rhythm} from '@kevinastone/theme-blog';

import {GatsbyImageSharpFluidFragment, HeroFragment} from '../../graphqlTypes';

const backgroundImage = (img: GatsbyImageSharpFluidFragment) => css`
  background-image: url(${img.src}), url(${img.base64});
`;

interface ContainerProps {
  img: GatsbyImageSharpFluidFragment | null | undefined;
}

const Container = styled.div<ContainerProps>`
  background-position: center;
  background-size: cover;
  color: white;
  margin-bottom: ${rhythm(1.5)};
  padding: ${rhythm(2)} 0;
  text-align: center;
  ${props => props.theme.shadow}
  ${props => (props.img ? backgroundImage(props.img) : props.theme.gradient)}
`;

type Props = {
  children: React.ReactNode;
} & HeroFragment;

export default ({hero, children}: Props) => {
  const img = idx(hero, _ => _.childImageSharp.fluid);
  return <Container img={img}>{children}</Container>;
};

export const query = graphql`
  fragment Hero on MarkdownRemarkFrontmatter {
    hero {
      childImageSharp {
        fluid(
          quality: 80
          maxWidth: 1920
          duotone: {highlight: "#ffcc5c", shadow: "#ff6f69", opacity: 50}
        ) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`;
