export type Result<T> = T | Error;

export function isError<T>(result: Result<T>): result is Error {
  return result instanceof Error;
}

export function isSuccess<T>(result: Result<T>): result is T {
  return !isError(result);
}

function errorify(error: any): Error {
  if (error instanceof Error) {
    return error;
  }

  return new Error(error);
}

export async function asResult<T>(promise: Promise<T>): Promise<Result<T>> {
  return promise.catch(error => errorify(error));
}
