import {css, SerializedStyles} from '@emotion/core';
import styled from '@emotion/styled';

const justifyMap = {
  end: 'flex-end',
  'space-around': 'space-around',
  'space-between': 'space-between',
  start: 'flex-start',
};

const alignMap = {
  ...justifyMap,
  stretch: 'stretch',
};

interface Props {
  column?: 'column' | 'row';
  justify?: keyof typeof justifyMap;
  align?: keyof typeof alignMap;
  wrap?: boolean;
  grow?: boolean;
}

export function box(props: Props): SerializedStyles {
  return css`
    display: flex;
    flex-direction: ${props.column ? 'column' : 'row'};
    justify-content: ${props.justify ? justifyMap[props.justify] : 'center'};
    align-items: ${props.align ? alignMap[props.align] : 'center'};
    flex-wrap: ${props.wrap ? 'wrap' : ' no-wrap'};
    flex-grow: ${props.grow ? 1 : 0};
  `;
}

const Box = styled.div<Props>`
  ${box};
`;

export const Row = styled(Box)`
  flex-direction: row;
`;

export const Column = styled(Box)`
  flex-direction: column;
`;

export default Box;
