import theme, {rhythm} from '@kevinastone/theme-blog';
import styled from '@kevinastone/theme-blog/styled';
import {ThemeProvider} from 'emotion-theming';
import {graphql, useStaticQuery} from 'gatsby';
import React from 'react';

import Fixed from './fixed';
import Header from './header';

interface Props {
  children: React.ReactNode;
  header?: React.ReactNode;
}

const Footer = styled(Fixed)`
  text-align: center;
  padding: ${rhythm(1)};
`.withComponent('footer');

const Layout = ({children, header}: Props) => {
  const {
    site: {
      siteMetadata: {title, author},
    },
  } = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
          author
        }
      }
    }
  `);

  return (
    <ThemeProvider theme={theme}>
      <Header siteTitle={title} />
      <main>
        {header}
        <Fixed>{children}</Fixed>
      </main>
      <Footer>
        Â© {author} {new Date().getFullYear()}, Built with
        {` `}
        <a href="https://www.gatsbyjs.org">Gatsby</a>
      </Footer>
    </ThemeProvider>
  );
};

Layout.defaultProps = {
  header: null,
};

export default Layout;
