import styled from '@kevinastone/theme-blog/styled';
import {globalHistory as history} from '@reach/router';
import React, {useEffect, useReducer} from 'react';

import {Actions, activateSelection, setQuery} from '../actions';
import {SearchResult} from '../api';
import reducer from '../reducers';

import Input from './input';
import Results from './results';

function curry(fn: (...args: any[]) => any, ...args: any[]) {
  return (...nArgs: any[]) => fn(...[...args, ...nArgs]);
}

const Form = styled.form`
  margin: 0;
  position: relative;
  width: 100%;
`;

interface Props {
  api: (query: string) => Promise<SearchResult>;
}

function SearchForm({api}: Props) {
  const [{query, results, selection}, dispatch] = useReducer(
    reducer,
    // @ts-ignore: Allow undefined for initial state
    undefined,
    reducer,
  );

  useEffect(() => history.listen(() => dispatch(Actions.reset())), [dispatch]);

  const onSubmit = (event: React.SyntheticEvent) => {
    event.preventDefault();
    return false;
  };

  const onKeyUp = (event: React.KeyboardEvent) => {
    switch (event.which) {
      case 13:
        event.preventDefault();
        activateSelection(selection, results);
        break;
      case 38:
        event.preventDefault();
        dispatch(Actions.previousSelection());
        break;
      case 40:
        event.preventDefault();
        dispatch(Actions.nextSelection());
        break;

      default:
      // Do nothing
    }
  };

  return (
    <Form onSubmit={onSubmit} onKeyUp={onKeyUp}>
      <Input value={query} onChange={curry(setQuery, dispatch, api)} />
      <Results results={results} selection={selection.current} />
    </Form>
  );
}

export default SearchForm;
