import React from 'react';
import styled from '@kevinastone/theme-blog/styled';
import {Link} from 'gatsby';
import {mix} from 'polished';
import {SearchResult} from '../api';

interface StyleProps {
  selected: boolean;
}

interface ResultProps {
  result: SearchResult;
}

const ListItem = styled.li<StyleProps>`
  background-color: ${props =>
    props.selected
      ? mix(0.8, props.theme.colors.background, props.theme.colors.primary)
      : props.theme.colors.background};
  margin: 0;
  padding: 0.8em;
  width: 100%;
`;

export default ({selected, result, ...props}: StyleProps & ResultProps) => (
  <ListItem selected={selected} {...props}>
    <Link to={result.url}>{result.title}</Link>
  </ListItem>
);
