import idx from 'idx.macro';
import * as lunr from 'lunr';

type Result = any;

interface LunrStore {
  [propName: string]: Result;
}

declare global {
  interface Window {
    __LUNR__: {
      __loaded: Promise<{en: {index: lunr.Index; store: LunrStore}}>;
    };
  }
}

export interface SearchResult {
  title: string;
  url: string;
  ref: string;
}

export default async function search(query: string): Promise<SearchResult[]> {
  const data = idx(await idx(window, _ => _.__LUNR__.__loaded), _ => _.en);
  if (!query || !data) {
    return [];
  }

  const {index, store} = data;

  if (![query, index, store].every(Boolean)) {
    return [];
  }

  return index
    .query(q => {
      const tokens = lunr.tokenizer(query);

      q.term(tokens, {boost: 100, usePipeline: true});
      q.term(tokens, {
        boost: 10,
        usePipeline: false,
        wildcard: lunr.Query.wildcard.TRAILING,
      });
      q.term(tokens, {boost: 1});
    })
    .map(({ref}) => ({ref, ...store[ref]}));
}
