import {Row} from '@kevinastone/flex-box';
import {rhythm} from '@kevinastone/theme-blog';
import styled from '@kevinastone/theme-blog/styled';
import {Link as BaseLink} from 'gatsby';
import * as React from 'react';

import Fixed from './fixed';
import Search from './search/app';

interface Props {
  siteTitle: string;
}

const HeaderContainer = styled.header`
  ${props => props.theme.gradient}
  ${props => props.theme.shadow}
`;

const Link = styled(BaseLink)`
  color: white;
  &,
  &:hover {
    text-decoration: none;
  }
`;

const NavBar = styled(Fixed)`
  padding-top: ${rhythm(1 / 2)};
  padding-bottom: ${rhythm(1 / 2)};
`;

const HeaderElement = styled.div`
  margin: 0;
  max-width: calc(50% - ${rhythm(1 / 4)});
`;

const Brand = HeaderElement.withComponent('h3');

const Header = ({siteTitle}: Props) => (
  <HeaderContainer>
    <NavBar>
      <Row justify="space-between">
        <Brand>
          <Link to="/">{siteTitle}</Link>
        </Brand>
        <HeaderElement>
          <Search />
        </HeaderElement>
      </Row>
    </NavBar>
  </HeaderContainer>
);

export default Header;
