import React from 'react';
import styled from '@kevinastone/theme-blog/styled';
import {rhythm} from '@kevinastone/theme-blog';
import {graphql} from 'gatsby';
import {HeadingFragment} from '../../graphqlTypes';
import Fixed from '../fixed';
import Hero from './hero';

const SubHeading = styled.p`
  margin-top: ${rhythm(-1 / 2)};
`;

export default ({title, date, hero}: HeadingFragment) => (
  <Hero hero={hero}>
    <Fixed>
      <h1>{title}</h1>
      <SubHeading>{date}</SubHeading>
    </Fixed>
  </Hero>
);

export const query = graphql`
  fragment Heading on MarkdownRemarkFrontmatter {
    title
    date(formatString: "MMMM DD, YYYY")
    ...Hero
  }
`;
