export default <
  TArgs extends any[],
  TReturn extends any,
  TFunc extends (...args: TArgs) => Promise<TReturn>
>(
  func: TFunc,
  wait: number,
  error: any = undefined,
): ((...args: TArgs) => Promise<TReturn>) => {
  let latest: any;

  return async (...args: TArgs) => {
    return new Promise<TReturn>((resolve, reject) => {
      latest = resolve;
      setTimeout(async () => {
        if (latest === resolve) {
          resolve(await func(...args));
        } else {
          reject(error);
        }
      }, wait);
    });
  };
};
