import {darken, lighten} from 'polished';
import Typography from 'typography';
import sutroTheme from 'typography-theme-sutro';
import theme from './theme';

interface Props {
  rhythm: (input: number) => any;
  scale: (input: number) => any;
}

sutroTheme.overrideThemeStyles = ({rhythm, scale}: Props) => ({
  a: {
    color: theme.colors.primary,
  },
  code: {
    'background-color': darken(0.04, theme.colors.background),
    'border-radius': rhythm(1 / 16),
    color: lighten(0.1, sutroTheme.bodyColor),
    padding: rhythm(1 / 8),
    ...scale(-1 / 8),
  },
});

const typography = new Typography(sutroTheme);

export default typography;
export const {rhythm, scale} = typography;
