import React from 'react';
import {css} from '@emotion/core';
import styled from '@kevinastone/theme-blog/styled';
import {rhythm} from '@kevinastone/theme-blog';
import {Theme} from '@kevinastone/theme-blog/theme';

const active = (theme: Theme) => css`
  background-color: ${theme.colors.background};
  border-color: transparent;
  color: ${theme.colors.primary};
`;

const Input = styled.input`
  background-color: transparent;
  border: 1px solid ${props => props.theme.colors.accent};
  border-radius: ${rhythm(1 / 8)};
  color: white;
  margin: 0;
  outline: none;
  padding: 0 0.8em;
  transition: background-color 0.5s;
  width: 100%;
  &:focus,
  &:hover {
    ${props => active(props.theme)}
  }
  ${props => props.value && active(props.theme)}
`;

interface Props {
  value: string;
  onChange: (value: string) => void;
}

export default ({value, onChange}: Props) => {
  const ref = React.useRef<HTMLInputElement>(null);

  return (
    <Input
      ref={ref}
      type="search"
      name="query"
      placeholder="Search"
      autoComplete="off"
      autoCorrect="off"
      autoCapitalize="off"
      spellCheck={false}
      value={value}
      onChange={event => onChange(event.target.value)}
      onFocus={() => setTimeout(() => ref.current && ref.current.select())}
    />
  );
};
