import useMeasure from '@kevinastone/use-measure';
import * as React from 'react';
import {
  animated,
  useTransition,
  OpaqueInterpolation,
  TransitionKeyProps,
} from 'react-spring';

interface Props {
  height: OpaqueInterpolation<number>;
  children: React.ReactNode;
}

function AccordianItem({height, children, ...props}: Props) {
  const [bind, {height: viewHeight}] = useMeasure<HTMLDivElement>();

  return (
    <animated.div
      style={{
        // @ts-ignore: interpolate not resolving correctly
        height: height.interpolate({output: [0, viewHeight]}),
      }}
      {...props}
    >
      <animated.div {...bind}>{children}</animated.div>
    </animated.div>
  );
}

type KeyFunc<TItem> =
  | ((item: TItem) => TransitionKeyProps)
  | TransitionKeyProps[]
  | TransitionKeyProps
  | null;

type RenderFunc<TItem> = (item: TItem, index: number) => React.ReactNode;

export default function useAccordian<TItem>(
  items: TItem[] | TItem | null | undefined,
  keyFunc: KeyFunc<TItem>,
  wrapperProps: any,
): (render: RenderFunc<TItem>) => React.ReactNode {
  const transitions = useTransition(items, keyFunc, {
    enter: {height: 1},
    from: {height: 0},
    leave: {height: 0},
    trail: 50,
  });

  return render =>
    transitions.map(({key, item, props}, index) => (
      <AccordianItem key={key} {...wrapperProps} {...props}>
        {render(item, index)}
      </AccordianItem>
    ));
}
