import {DiscussionEmbed} from 'disqus-react';
import {graphql, useStaticQuery} from 'gatsby';
import * as React from 'react';

interface Props {
  id: string;
  title: string;
}

function Comments({id, title}: Props) {
  const {
    site: {
      siteMetadata: {disqusShortname},
    },
  } = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          disqusShortname
        }
      }
    }
  `);

  if (!disqusShortname) {
    return null;
  }

  return <DiscussionEmbed shortname={disqusShortname} config={{id, title}} />;
}

export default Comments;
